.add-order {
  max-width: 600px;
  margin: 0 auto;
}

.add-order h2 {
  color: #333;
  margin-bottom: 30px;
}

.order-form {
  background: white;
  padding: 30px;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.disabled-input {
  background-color: #e9ecef;
  cursor: not-allowed;
}

.form-actions {
  display: flex;
  gap: 10px;
  margin-top: 30px;
}

.form-actions .btn {
  flex: 1;
  text-align: center;
}

.form-actions a {
  display: block;
  padding: 8px 16px;
  text-decoration: none;
}
